import React from 'react'
import { BrowserRouter as Router, Route } from "react-router-dom"
import Layout from './components/Layout'
import Blog from './components/Blog'
import Demo from './components/Demo'
import PostMonthView from './components/PostMonthView'
import PostSearchView from './components/PostSearchView'
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import { createMuiTheme } from '@material-ui/core/styles';
import hljs from 'highlight.js/lib/highlight'
import 'highlight.js/styles/github.css'
import python from 'highlight.js/lib/languages/python'
hljs.registerLanguage('python', python)


const theme = createMuiTheme({
    typography: {
        fontFamily: `"Roboto", "Helvetica", "Arial", sans-serif`,
        useNextVariants: true,
    }
});

const App = () => {
    return (
        <MuiThemeProvider theme={theme}>
            <Router>
                <Layout>
                    <Route exact path="/" component={Blog} />
                    <Route path="/demo" component={Demo} />
                    <Route path="/about" component={About} />
                    <Route path="/posts/search/:keyword" component={PostSearchView} />
                    <Route exact path="/posts/:date" component={PostMonthView} />
                </Layout>
            </Router>
        </MuiThemeProvider>
    )
}

const About = () => <h2>About</h2>;

export default App;