import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import { Link as RouteLink } from 'react-router-dom'
import * as R from 'ramda'
import moment from 'moment'

const styles = theme => ({
    sidebarAboutBox: {
        padding: theme.spacing.unit * 2,
        backgroundColor: theme.palette.grey[200],
    },
    sidebarSection: {
        marginTop: theme.spacing.unit * 3,
    },

})

const getLastNMonths = (n) => (
    R.range(0, n).map((i) => {
        const m = moment().subtract(i, 'months')
        return {
            'str': m.format("MMMM YYYY"),
            'url': '/posts/' + m.format("YYYYMM"),
        }
    })
)

const archives = getLastNMonths(12)

const socials = [
    {
        name: 'GitHub',
        url: "https://github.com/sbl1996"
    },
    {
        name: 'Zhihu',
        url: "https://www.zhihu.com/people/chen-bi-luo-74/activities"
    },
]


const Sidebar = withStyles(styles)(({ classes }) => (
    <Grid item xs={12} md={4}>
        <Paper elevation={0} className={classes.sidebarAboutBox}>
            <Typography variant="h6" gutterBottom>
                About
            </Typography>
            <Typography>
                I am a college graduate of Lanzhou University in the gap year. I work on machine learning
                and deep learning with Python (mainly PyTorch, also Keras).<br />
                I am also interested in functional programming languages like Haskell, Scala and Elixir.
                Full stack web development is my main way to practice them.
            </Typography>
        </Paper>
        <Typography variant="h6" gutterBottom className={classes.sidebarSection}>
            Archives
        </Typography>
        {archives.map(archive => (
            <Typography key={archive.str}>
                <Link color="inherit" component={RouteLink} to={archive.url}>
                    {archive.str}
                </Link>

            </Typography>
        ))}
        <Typography variant="h6" gutterBottom className={classes.sidebarSection}>
            Social
        </Typography>
        {socials.map(social => (
            <Typography key={social.name} noWrap>
                <Link href={social.url} color="inherit" target="_blank" rel="noreferrer">
                    {social.name}
                </Link>
            </Typography>
        ))}
    </Grid>
))

export default withStyles(styles)(Sidebar);