import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { CircularProgress } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Sidebar from './Sidebar'
import * as R from 'ramda'
import Markdown from './Markdown'



const styles = theme => ({
    mainGrid: {
        marginTop: theme.spacing.unit * 3,
    },
    markdown: {
        padding: `${theme.spacing.unit * 3}px 0`,
    },
});


const PostList = withStyles(styles)(({ classes, posts, loading }) => {
    return (
        <Grid container spacing={40} className={classes.mainGrid}>
            {loading ?
                <Grid item xs={12} md={8}>
                    <CircularProgress></CircularProgress>
                </Grid>
                :
                <MainContent post={posts.length > 0 ? posts[0] : { content: '' }} />
            }
            <Sidebar />
            {
                R.tail(posts).map((post) => (
                    <MainContent key={post.title} post={post} />
                ))
            }
        </Grid>
    );
})


const MainContent = withStyles(styles)(({ classes, post }) => {
    return (
        <Grid item xs={12} md={8}>
            <div>
                <Divider />
                <Markdown className={classes.markdown}>
                    {post.content}
                </Markdown>
            </div>
        </Grid>
    )
})

export default PostList;