import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { getPostsByMonth } from '../api/posts'
import PostList from './PostList'



const styles = theme => ({
    markdown: {
        padding: `${theme.spacing.unit * 3}px 0`,
    },
});

const PostMonthView = ({ classes, match }) => {
    const month = match.params.date
    const [posts, setPosts] = useState([]);
    const [loading, setLoading] = useState(true)

    const fetchPosts = async (month) => {
        setLoading(true)
        const posts = await getPostsByMonth(month)
        setPosts(posts)
        setLoading(false)
    }

    useEffect(() => {
        fetchPosts(month)
        window.scrollTo(0, 0);
    }, [month])

    return (
        <PostList posts={posts} loading={loading} classes={classes} />
    )
}

export default withStyles(styles)(PostMonthView);