import axios, { post } from 'axios'

export const getResume = async () => {
    const url = 'file/resume'
    const rep = await axios({
        method: 'get',
        url,
        responseType: 'blob',

    })
    return rep.data
}

export const predictCaptcha = async (file) => {
    const url = '/api/captcha/detection'
    const formData = new FormData();
    formData.append('img', file)
    const config = {
        headers: {
            'content-type': 'multipart/form-data'
        }
    }
    const rep = await post(url, formData, config)
    return rep.data
}

export const genCaptcha = async () => {
    const url = '/api/captcha/generate?t=' + new Date().getTime()
    const rep = await axios({
        method: 'get',
        url,
        responseType: 'blob',

    })
    return rep.data
}